* {
	box-sizing:border-box;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }
  
  *:before,
  *:after {
	  box-sizing:border-box;
  }
  
  html{
	  height: 100%;
	  -ms-text-size-adjust: 100%;
		-webkit-text-size-adjust: 100%;
		box-sizing: border-box;
  }
  
  
  *:focus {
	  outline: none;
	  -moz-outline-style: none;
  }
  
  :-moz-any-link:focus {
	  outline: 1px dotted invert;
  }
  
  :-moz-any-link:focus {
	  outline: none;
  }
  
  textarea,input {
	  box-shadow:none;
	  font-family: inherit;
	  font-size: inherit;
	  line-height: inherit;
  }
  
  a{
	  transition: color 0.3s linear;
	  text-decoration: none;
	  &:not([class]){
		  color: #E4002B;
		  &:hover{
			  color: #91001B;
		  }
	  }
  }
  
  
  body {
	  margin: 0;
	  height: 100%;
	  background: $default-site-bg;
	  -webkit-font-smoothing: antialiased;
	  font-size: $default-size-font;
	  font-family: $default-font;
	  color: $base-color;
	  font-weight: 300;
	  display: flex;
		flex-direction: column;
		&.hidden{
			overflow: hidden;
		}
  }
  
  textarea,input {
	  box-shadow: none;
	  font-family: inherit;
	  font-size: inherit;
	  line-height: inherit;
  }
  
  
  ::-webkit-input-placeholder {color:#a5a8ab;transition:all 0.3s ease;}
  ::-moz-placeholder          {color:#a5a8ab;transition:all 0.3s ease;}
  :-moz-placeholder           {color:#a5a8ab;transition:all 0.3s ease;}
  :-ms-input-placeholder      {color:#a5a8ab;transition:all 0.3s ease;}
  
  
  
  :focus::-webkit-input-placeholder {color: transparent}
  :focus::-moz-placeholder          {color: transparent}
  :focus:-moz-placeholder           {color: transparent}
  :focus:-ms-input-placeholder      {color: transparent}